const AvgStar = (color, ratingFloat, size) => {
    return <div className="tb_avg_rating_ico__ tb__icon tb-star-fill" style={{ fontSize: size }}><div className="tb_avg_rating_ico_fill__ tb__icon tb-star-fill" style={{ color: color, width: ratingFloat * 10 + '%', fontSize: size }}><div></div> </div></div>
}
const AverageRating = ({ rating, size }) => {
    const rate = rating > 5 ? 5 : rating
    const grayRate = Math.trunc(5 - rate)
    const ratingInt = Math.trunc(rate)
    const ratingFloat = String(rate).split('.')[1]
    return <div className="tb_avg_rating__" aria-label="Ratings" role="status">
        {
            ratingInt ? [...Array(ratingInt)].map((el, index) => <div key={index} className="tb_avg_rating_ico__ tb__icon tb-star-fill" style={{ color: '#F8B90C', fontSize: size }}><div></div></div>) : null
        }
        {
            ratingFloat > 0 ? AvgStar('#F8B90C', ratingFloat, size) : null
        }
        {
            grayRate ? [...Array(grayRate)].map((el, index) => <div key={index} className="tb_avg_rating_ico__ tb__icon tb-star-fill" style={{ fontSize: size }}><div></div></div>) : null
        }
    </div>
}

export default AverageRating